.full-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  padding: 20px;
  background: transparent;
  color: black;
}

.full-content {
  text-align: center;
}

.countdown-title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.countdown-time {
  font-size: 40px;
  margin-bottom: 30px;
}

.program-title {
  font-size: 1.8rem;
  font-weight: bold;
  text-decoration: underline;
  margin-bottom: 20px;
}

/* --- Event Cards Container --- */
.events-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
}

/* --- Event Card Styling --- */
.event-card {
  width: 180px;
  transition: all 0.4s ease-in-out;
  text-align: center;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  overflow: hidden;
  border-radius: 10px; /* Ensure rounded corners for the card */
}

/* Remove any default image borders and set a smooth transition */
.event-card img {
  width: 100%;
  height: auto;
  border-radius: 10px; /* Maintain rounded corners */
  transition: transform 0.3s ease-in-out, border-radius 0.3s ease-in-out;
  object-fit: cover;
}

/* Unselected (non-expanded) cards scale down and have reduced opacity */
.event-card:not(.expanded) {
  transform: scale(0.9);
  opacity: 0.8;
}

/* When an event card is expanded */
.event-card.expanded {
  width: 350px;
  transform: scale(1);
  transition: all 0.4s ease-in-out;
  border-radius: 10px; /* Maintain rounded corners */
  overflow: hidden; /* Ensure image doesn't overflow the card */
}

/* In the expanded card, enlarge the image while keeping rounded corners */
.event-card.expanded img {
  transform: scale(1.1);
  margin-bottom: 10px;
  border-radius: 10px; /* Maintain rounded corners */
}

/* --- Text Styling --- */
.event-time {
  font-size: 16px;
  transition: font-size 0.3s ease-in-out;
}

.event-card:not(.expanded) .event-time {
  font-size: 16px;
}

.event-title {
  margin-top: 8px;
  font-size: 18px;
  font-weight: bold;
  transition: font-size 0.3s ease-in-out;
}

.event-card:not(.expanded) .event-title {
  font-size: 16px;
}

/* --- Details Section --- */
.event-details {
  display: none;
  text-align: left;
  font-size: 16px;
  margin-top: 10px;
  line-height: 1.5;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 8px; /* Optional: add slight rounding for details */
  padding: 5px;
}

/* Only show details for the expanded card */
.event-card.expanded .event-details {
  display: block;
}

/* --- Location Styling --- */
.event-location {
  display: none;
  text-align: left;
  font-size: 14px;
  margin-top: 5px;
  font-style: italic;
  color: #555;
}

.event-card.expanded .event-location {
  display: block;
} 
